
body{
  background-color: #111;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
    
  }
  .waviy {
    position: relative;
  }
.waviy span { 
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  position: relative;
  display: block;
  font-size: 40px;
  color: #fff;
  text-transform: uppercase;
  animation: flip 2s infinite;
  animation-delay: calc(.2s * var(--i))
}
@keyframes flip {
  0%,80% {
    transform: rotateY(360deg) 
  }
}
#loadingtext{
    padding-bottom: 30px;

    font-size:18px
}
@media screen and (max-width: 1040px) {
	.waviy span{
    font-size: 20px;


	}
	
}